.btnCont {
  display: flex;
  background-color: white;
}

.btn {
  font-size: 14px;
  text-decoration: none;
  cursor: pointer;
  display: block;
  width: 90px;
  font-weight: bold;
  margin: 20px auto;
  text-align: center;
  color: white;

  padding: 10px;
  background-color: rgb(41, 100, 90);
  border-radius: 10px;
  border: 1px solid gray;

  transition: all 250ms ease;
}

.btn:hover {
  background: #1909ff;
  color: black;
}